import React, {Component} from 'react';
import {
    VictoryAxis,
    VictoryChart,
    VictoryGroup,
    VictoryLabel,
    VictoryLegend,
    VictoryLine,
    VictoryScatter
} from 'victory';
import './PoTGraph.css';

class PoTGraph extends Component {
    constructor(props) {
        super(props);
        this.state = {
            nonTiData: [{}],
            tiData: [{}]
        }
    }

    fetchModelData(modelNumber) {
        const apiString = `./api/v0.1/model/${modelNumber}`;
        fetch(apiString)
            .then(results => {
                    return results.json();
                }
            ).then(
            jsonified_data => {
                let nonTiData = jsonified_data[0];
                let tiData = jsonified_data[1];
                this.setState({
                    nonTiData: nonTiData,
                    tiData: tiData
                });
            }
        )
    }

    render() {
        this.fetchModelData(this.props.model);
        return (
            <div
                className={'price-over-time-chart'}
                style={{display: 'flex'}}
            >
                <h4>Price of {this.props.model} over time</h4>

                <VictoryChart>
                    <VictoryGroup>
                        <VictoryLine
                            name={'non-ti-line'}
                            data={this.state.nonTiData}
                            x={'created'}
                            y={'price'}
                            labels={({datum}) => `${datum.datestring}`}
                        />
                        <VictoryScatter
                            name={'non-ti-points'}
                            data={this.state.nonTiData}
                            x={'created'}
                            y={'price'}
                        />
                        {/*TODO: Refactor this lower logic, it violates DRY. Maybe component?*/}
                        {
                            this.state.tiData.length !== 0 &&
                            <VictoryLine
                                name={'ti-line'}
                                data={this.state.tiData}
                                x={'created'}
                                y={'price'}
                                labels={({datum}) => `${datum.datestring}`}
                            />
                        }

                        {
                            this.state.tiData.length !== 0 &&
                            <VictoryScatter
                                name={'ti-points'}
                                data={this.state.tiData}
                                x={'created'}
                                y={'price'}
                            />
                        }

                    </VictoryGroup>
                </VictoryChart>

            </div>
        );
    }
}


export default PoTGraph;